.about-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-bg {
    height: 100vh;
    width: 100%;
    object-fit: cover;
}

.about-txt-container {
    position: absolute;
    margin: auto;
    color: #0d1821;
}

.about-txt {
    text-align: left;
    font-size: 2em;
}